<template>
    <div class="ui-input-cell">
        <div class="title">
            {{title}}
        </div>
        <div class="input-box font-num">
            <slot name="input"></slot>
        </div>
        <div class="right">
            <slot name="right"></slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'inputCell',
        props: {
            value: {
                default: ''
            },
            title: {
                type: String,
                default: ''
            },
            inputType: {
                type: String,
                default: 'text'
            },
            placeholder: {
                type: String,
                default: ''
            },
            readonly: {
                type: Boolean,
                default: false
            }
        }
    }
</script>

<style lang="scss" scoped>
    .ui-input-cell{
        display: flex;
        justify-content: space-between;
        padding: 0 px2rem(28);
        height: px2rem(100);
        width: 100%;
        align-items: center;
        font-family: PingFangSC;
        font-size: px2rem(32);
        background-color: #ffffff;
        box-sizing: border-box;
        .title{
            width: px2rem(158);
            height: px2rem(100);
            line-height: px2rem(100);
            flex: none;
            color: #333333;
            margin-right: px2rem(8);
        }
        .input-box{
            height: px2rem(100);
            flex: 1;
            display: flex;
            align-items: center;
            input{
                border: none;
                outline: none;
                background-color: #ffffff;
                flex: 1;
                font-size: px2rem(32);
                &::-webkit-input-placeholder{
                    color: #9B9B9B;
                }
            }
        }
        .right{

        }
    }
</style>



