<template>
    <v-page :options="options">
        <div class="remittance-container">
            <div class="card-info font-num">
                <div class="top">
                    <p>转出账号</p>
                    <p class="card-no" @click="handleShowCardList">
                        <span>{{bankCardList[remittanceInfo.bankCardActiveIndex].bankCardNo | formatBankCard}}</span>
                        <i class="icon icon-list_icon_down-" :class="{ active: show && popupType === 'card' }"></i>
                    </p>
                </div>
                <div class="bottom">
                    <span class="txt">余额</span>
                    <span class="money">{{bankCardList[remittanceInfo.bankCardActiveIndex].balance | normalizeMoney}}元</span>
                </div>
            </div>
            <div class="amount">
                <div class="top">
                    <span class="txt">转账金额</span>
                    <p class="remittance-type">
                        <span class="type">{{tranAccountList[remittanceInfo.tranActiveIndex].type}}</span>
                        <span class="change-type" @click="showTranAccount">更换</span>
                    </p>
                </div>
                <div class="bottom">
                    <span class="rem-sign">¥</span>
                    <div class="input-box font-num">
                        <input type="text"
                               v-model="remittanceInfo.amount"
                               @input="limitInput"
                        >
                    </div>
                </div>
            </div>
            <div class="tips" v-show="showTips">
                <div class="symbol-wrap">
                    <v-symbol :name="'warn'"></v-symbol>
                </div>
                <p>所输金额不能超过余额范围!</p>
            </div>
            <ul class="input-group font-num">
                <li class="item">
                    <v-input-cell :title="'收款人'">
                        <input type="text"
                               placeholder="请输入收款人姓名"
                               v-model="remittanceInfo.recName"
                               slot="input"
                               maxlength="15"
                        >
                        <div class="icon-wrap" slot="right" @click="jumpPageRegister">
                            <i class="icon icon-head_default"></i>
                        </div>
                    </v-input-cell>
                </li>
                <li class="item">
                    <v-input-cell :title="'收款账号'">
                        <input type="tel"
                               placeholder="请输入收款人账号"
                               v-model="remittanceInfo.recCardNo"
                               slot="input"
                               maxlength="20"
                        >
                    </v-input-cell>
                </li>
                <li class="item">
                    <v-input-cell :title="'收款银行'">
                        <input type="text"
                               placeholder="选择银行"
                               v-model="remittanceInfo.recBank"
                               readonly
                               slot="input"
                        >
                        <div class="icon-wrap" slot="right" @click="jumpRecBankPage">
                            <i class="icon icon-list_icon_path-"></i>
                        </div>
                    </v-input-cell>
                </li>
                <li class="item" v-show="showRecPoint">
                    <v-input-cell :title="'收款网点'">
                        <input type="text"
                               placeholder="选择网点"
                               v-model="remittanceInfo.recPoint"
                               readonly
                               slot="input"
                        >
                        <div class="icon-wrap" slot="right" @click="jumpRecPointPage">
                            <i class="icon icon-list_icon_path-"></i>
                        </div>
                    </v-input-cell>
                </li>
                <li class="item">
                    <v-input-cell :title="'短信通知'">
                        <input type="tel"
                               placeholder="收款人手机号（可不填）"
                               v-model="remittanceInfo.recPhone"
                               slot="input"
                               maxlength="11"
                        >
                    </v-input-cell>
                </li>
            </ul>
            <div class="postscript">
                <v-input-cell :title="'附言'">
                    <input type="text"
                           placeholder="转账备注（可不填）"
                           v-model="remittanceInfo.postScript"
                           slot="input">
                    <div class="icon-wrap" slot="right" @click="handleShowPostscript">
                        <i class="icon icon-list_icon_down-" :class="{ active: showPostscript }"></i>
                    </div>
                </v-input-cell>
                <div class="list-wrap">
                    <ul class="postscript-list">
                        <li class="item" v-for="(item, index) in postscriptList"
                            :key="index"
                            @click="setPostScript(item.txt)"
                        >
                            {{item.txt}}
                        </li>
                    </ul>
                </div>
            </div>
            <div class="btn-wrap" :class="{ active: showPostscript }">
                <v-linear-button :btnTxt="'下一步'" :disabled="!canNext" @click="jumpTransferPage"></v-linear-button>
            </div>
        </div>
        <v-popup v-model="show">
            <div class="popup-wrap">
                <div class="title-wrap">
                    <h1 class="title">{{popupInfo.title}}</h1>
                    <span class="popup-cancel" @click="closePopup">取消</span>
                </div>
                <div class="list-wrap font-num">
                    <ul class="tran-type-list list" v-show="popupType === 'tran'">
                        <li class="item" v-for="(item, index) in tranAccountList" :key="index"
                            @click="chooseTranAccountType(index)">
                            <div class="left">
                                <span class="top">{{item.type}}</span>
                                <span class="bottom">{{item.tips}}</span>
                            </div>
                            <div class="right">
                                <i class="icon icon-check" v-show="index == remittanceInfo.tranActiveIndex"></i>
                            </div>
                        </li>
                    </ul>
                    <ul class="card-list list" v-show="popupType === 'card'">
                        <li class="item" v-for="(item, index) in bankCardList" :key="index" @click="chooseCard(index)">
                            <div class="left">
                                <div class="symbol-wrap">
                                    <v-symbol :name="item.bankIcon"></v-symbol>
                                </div>
                                <div class="cardInfo">
                                    <span class="top">{{item.name}}</span>
                                    <p class="bottom">
                                        <span>{{`${item.bankName}${item.bankCardType}`}}</span>
                                        <span class="card-no">{{item.bankCardNo | formatBankCard}}</span>
                                    </p>
                                </div>
                            </div>
                            <div class="right">
                                <i class="icon icon-check" v-show="remittanceInfo.bankCardActiveIndex == index"></i>
                            </div>
                        </li>
                        <li class="item add" @click="jumpAddBankCard">
                            <v-symbol :name="'add'"></v-symbol>
                            <span class="txt">添加卡片</span>
                        </li>
                    </ul>
                </div>
            </div>
        </v-popup>
        <v-alert v-model="showAlert">
            <p class="alert-txt">{{alertMsg}}</p>
        </v-alert>
    </v-page>
</template>

<script>
    import {vInputCell} from '../../components/inputCell'
    import {vLinearButton} from '../../components/linearButton'
    export default {
        name: 'remittanceInput',
        components: {
            vInputCell,
            vLinearButton
        },
        computed: {
            showTips () {
                return parseFloat(this.remittanceInfo.amount) > parseFloat(this.remittanceInfo.balance)
            },
            // 显示收款网点
            showRecPoint () {
                return parseFloat(this.remittanceInfo.amount) > parseFloat(this.maxShowRecPoint)
            },
            // 控制下一步按钮是否可点击
            canNext () {
                return this.remittanceInfo.amount !== '' &&
                    this.remittanceInfo.tranType !== '' &&
                    this.remittanceInfo.recName !== '' &&
                    this.remittanceInfo.recCardNo !== '' &&
                    this.remittanceInfo.recBank !== '' &&
                    ((this.showRecPoint && this.remittanceInfo.recPoint !== '') ||
                    !this.showRecPoint)
            }
        },
        data () {
            return {
                // 页面参数
                options: {
                    scroll: true
                },
                // 显示附言
                showPostscript: false,
                // 供用户选择的附言
                postscriptList: [
                    {
                        txt: '工资'
                    },
                    {
                        txt: '还款'
                    },
                    {
                        txt: '房租'
                    },
                    {
                        txt: '生活费'
                    },
                    {
                        txt: '贷款'
                    }
                ],
                // 转账信息
                remittanceInfo: {
                    amount: '',
                    balance: '122017.18',
                    // 转出账号
                    tranNo: '',
                    // 转账类型
                    tranType: '实时转账',
                    // 转账类型编号
                    tranTypeNum: '0',
                    // 收款人
                    recName: '',
                    // 收款人账号,
                    recCardNo: '',
                    // 收款银行
                    recBank: '测试银行',
                    // 收款网点
                    recPoint: '测试收款网点',
                    // 收款人手机号
                    recPhone: '',
                    // 附言
                    postScript: '',
                    // 转账方式激活下标
                    tranActiveIndex: '0',
                    // 银行卡激活下标
                    bankCardActiveIndex: '0'
                },
                // 转账类型选择列表
                tranAccountList: [
                    {
                        type: '实时转账',
                        tips: '',
                        typeNum: '0',
                        isActive: false,
                    },
                    {
                        type: '次日转账',
                        tips: '将于次日（0点之后）汇出款项',
                        typeNum: '1',
                        isActive: false
                    },
                    {
                        type: '普通转账',
                        tips: '将于24小时之内汇出款项',
                        typeNum: '2',
                        isActive: false
                    }
                ],
                // 转账银行卡列表
                bankCardList: [
                    {
                        bankIcon: 'zhongguominshengyinhang',
                        bankName: '民生银行',
                        bankCardNo: '6214000000000006666',
                        bankCardType: '储蓄卡',
                        name: '程璧荣',
                        balance: '123456789'
                    },
                    {
                        bankIcon: 'zhongguogongshangyinhang',
                        bankName: '工商银行',
                        bankCardNo: '6214 **** 6555',
                        bankCardType: '储蓄卡',
                        balance: '223456789',
                        name: '张三'
                    },
                    {
                        bankIcon: 'pufayinhang',
                        bankName: '浦发银行',
                        bankCardNo: '6214 **** 6444',
                        bankCardType: '储蓄卡',
                        balance: '129545',
                        name: '王五'
                    },
                    {
                        bankIcon: 'guangfayinhang',
                        bankName: '广发银行',
                        bankCardNo: '6214 **** 6333',
                        bankCardType: '储蓄卡',
                        balance: '999999999',
                        name: '冯宝宝'
                    },
                    {
                        bankIcon: 'dongyayinhang',
                        bankName: '东亚银行',
                        bankCardNo: '6214 **** 6222',
                        bankCardType: '储蓄卡',
                        balance: '78954',
                        name: '王也'
                    },
                    {
                        bankIcon: 'zhaoshangyinhang',
                        bankName: '招商银行',
                        bankCardNo: '6214 **** 6111',
                        bankCardType: '储蓄卡',
                        balance: '4399',
                        name: '张起灵'
                    }
                ],
                // 转账金额大于如下规定金额显示收款网点
                maxShowRecPoint: '100000',
                // 实时转账最大限额
                maxTran: '100000',
                // 是否显示弹出框
                show: false,
                // 是否显示alert提示框
                showAlert: false,
                // 底部弹出框类型 'tran' - 转账类型 'card' - 银行卡列表
                popupType: '',
                // 弹出框信息
                popupInfo: {
                    title: '选择转账方式'
                },
                // alert框提示信息
                alertMsg: '您的转账金额大于实时转账限额，已转为普通转账方式（24小时内汇出款项）',
                // 转账方式激活下标
                tranActiveIndex: '0',
                // 银行卡激活下标
                bankCardActiveIndex: '0'
            }
        },
        filters: {
            // 格式化  @return 5,00.00
            normalizeMoney: function (value) {
                if (!value) return ''
                value = parseFloat(value)
                value = value.toFixed(2)
                let valueStr = value.toString()
                let valueArr = valueStr.split('.')
                let val0 = valueArr[0].split('').reverse()
                let val1 = valueArr[1]
                let len = val0.length
                let arr = []
                for (let i = 0; i < len; i = i + 3) {
                    arr.unshift(val0.slice(i, i + 3).reverse().join(''))
                }
                return arr.join(',') + '.' + val1
            },
            // 格式化银行卡号
            formatBankCard (value) {
                if (!value) return ''
                value = value.toString()
                let reg = /^(\d{4})(.*)(\d{4})$/
                value = value.replace(reg, (match, p1, p2, p3) => {
                    return p1 + ' **** ' + p3
                })
                return value
            }
        },
        methods: {
            // 显示附言选择列表
            handleShowPostscript () {
                this.showPostscript = !this.showPostscript
            },
            // 限制金额输入
            limitInput (e) {
                let str = e.target.value
                let str0 = ''
                let str1 = ''
                str = str.replace(/[^0-9\.]/ig, '')
                if (str.indexOf('.') > -1) {
                    let index = str.indexOf('.')
                    str0 = str.slice(0, index)
                    str1 = str.slice(index + 1).toString().replace(/\./ig, '')
                    if (str1.length > 2) {
                        str1 = str1.slice(0, 2)
                    }
                    e.target.value = str0 + '.' + str1
                    this.remittanceInfo.amount = str0 + '.' + str1
                } else {
                    e.target.value = str
                    this.remittanceInfo.amount = str
                }
                this.isOverMaxTran()
            },
            // 序列化金额 @return 5,000.00
            reformatMoney (value) {
                if (!value) return ''
                value = parseFloat(value)
                value = value.toFixed(2)
                let valueStr = value.toString()
                let valueArr = valueStr.split('.')
                let val0 = valueArr[0].split('').reverse()
                let val1 = valueArr[1]
                let len = val0.length
                let arr = []
                for (let i = 0; i < len; i = i + 3) {
                    arr.unshift(val0.slice(i, i + 3).reverse().join(''))
                }
                return arr.join(',') + '.' + val1
            },
            // 显示转账类型
            showTranAccount () {
                this.popupType = 'tran'
                this.popupInfo.title = '选择转账方式'
                this.show = true
            },
            // 显示银行卡选择列表
            handleShowCardList () {
                this.popupType = 'card'
                this.popupInfo.title = '选择卡片'
                this.show = true
            },
            // 关闭popup
            closePopup () {
                this.show = false
            },
            // 选择转账方式
            chooseTranAccountType (index) {
                // 当前转账金额超出实时转账最大限额不选择
                if (parseFloat(this.maxTran) < parseFloat(this.remittanceInfo.amount) && index == 0)
                    return
                this.activeTranIndex('tranActiveIndex', 'tranAccountList', index)
                this.remittanceInfo.tranType = this.tranAccountList[this.remittanceInfo.tranActiveIndex].type
                this.remittanceInfo.tranTypeNum = this.tranAccountList[this.remittanceInfo.tranActiveIndex].typeNum
                this.closePopup()
            },
            // 选择银行卡
            chooseCard (index) {
                this.activeTranIndex('bankCardActiveIndex', 'bankCardList', index)
                this.remittanceInfo = Object.assign(this.remittanceInfo, this.bankCardList[this.bankCardActiveIndex])
                this.closePopup()
            },
            /*
             * 激活选择转账方式或者选择银行卡下标
             * @param {String} currenIndexKey - 当前激活下标对应的key
             * @param {String} listArrSourceKey - 当前弹出框渲染的数组key
             * @param {String} activeIndex - 当前选中的下标
             */
            activeTranIndex (currenIndexKey, listArrSourceKey, activeIndex) {
                this[currenIndexKey] = this[currenIndexKey] === '' ? 0 : this[currenIndexKey]
                let currentIndex = this[currenIndexKey]
//                this[listArrSourceKey][currentIndex].isActive = false
//                this[listArrSourceKey][activeIndex].isActive = true
                this.remittanceInfo[currenIndexKey] = activeIndex
            },
            // 判断转账金额是否大于规定实时转账最大限额
            isOverMaxTran () {
                // 只处理实时转账
                if (this.remittanceInfo.tranActiveIndex != 0)
                    return
                if (parseFloat(this.maxTran) < parseFloat(this.remittanceInfo.amount)) {
                    this.remittanceInfo.tranActiveIndex = '2'
                    this.showAlert = true
                }
            },
            // 跳转到选择网点
            jumpRecPointPage () {
                let jumpPageUrl = 'main/remittance/recPoint'
                this.jumpPage(jumpPageUrl)
            },
            // 跳转到收款银行
            jumpRecBankPage () {
                let jumpPageUrl = 'main/remittance/chooseRecBank'
                this.jumpPage(jumpPageUrl)
            },
            // 跳转到转账汇款页面
            jumpTransferPage () {
                let jumpUrl = '/main/remittance/transferRemittance'
                this.remittanceInfo.tranNo = this.bankCardList[this.bankCardActiveIndex].bankCardNo
                this.jumpPage(jumpUrl)
            },
            // 跳转到收款人名册
            jumpPageRegister () {
                let jumpUrl = '/main/remittance/receiverRegister'
                this.jumpPage(jumpUrl)
            },
            // 跳转到添加银行卡
            jumpAddBankCard () {
                let jumpUrl = '/login/addBankCard/addBankCard'
                this.jumpPage(jumpUrl)
            },
            // 跳转页面
            jumpPage (jumpPageUrl) {
                let obj = {
                    path: jumpPageUrl,
                    params: this.remittanceInfo
                }
                window.T.go(obj)
            },
            // 设置附言
            setPostScript (txt) {
                this.remittanceInfo.postScript = txt
            },
            // 获取页面传递参数
            getPageParams () {
                window.T.Native.getPageParams().then(({params}) => {
                    this.remittanceInfo = Object.assign(this.remittanceInfo, params)
                })
            }
        },
        created () {
            this.getPageParams()
        }
    }
</script>

