                -moz-transform: translate3d(0, 0, 0);
                -ms-transform: translate3d(0, 0, 0);
                -o-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
            }
        }
    }
    .active {
        transform: rotate(180deg);
    }
    .icon{
        transition: all .2s;
    }
    /*底部弹出选择框*/
    .popup-wrap {
        background-color: #ffffff;
        .title-wrap {
            height: px2rem(120);
            padding: 0 px2rem(28);
            display: flex;
            align-items: center;
            position: relative;
            border-bottom: 1px solid #DBDBDB;
            flex-direction: row-reverse;
            .title {
                font-family: PingFangSC-Medium;
                color: $font-black;
                font-size: px2rem(36);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate3d(-50%, -50%, 0);
            }
            .popup-cancel {
                color: #9B9B9B;
                font-size: px2rem(28);
            }
        }
        .list-wrap {
            max-height: px2rem(600);
            overflow-y: scroll;
            .list {
                .item {
                    min-height: px2rem(120);
                    background-color: #ffffff;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    border-bottom: 1px solid #DBDBDB;
                    padding: px2rem(20) px2rem(28);
                    box-sizing: border-box;
                    .left {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        .top {
                            margin-bottom: px2rem(2);
                            font-size: px2rem(32);
                            line-height: px2rem(44);
                            font-family: PingFangSC-Regular;
                        }
                        .bottom {
                            font-size: px2rem(24);
                            line-height: px2rem(34);
                            color: #9B9B9B;
                        }
                    }
                    .right {
                        width: px2rem(36);
                        height: px2rem(36);
                        margin-left: px2rem(20);
                        .icon-check {
                            display: block;
                            width: px2rem(36);
                            height: px2rem(36);
                            background: url("./images/icon-check.png") no-repeat center;
                            background-size: 90%;
                        }
                    }
                    &:last-child {
                        border-bottom: none;
                    }
                    @include bg(#fff);
                }
            }
            .card-list {
                .item {
                    .left {
                        align-items: center;
                        flex-direction: row;
                        .symbol-wrap {
                            margin-right: px2rem(10);
                            display: flex;
                            align-items: center;
                            .ui-symbol {
                                height: px2rem(78);
                                width: px2rem(78);
                            }
                        }
                        .card-no {

                        }
                    }
                }
                .item.add {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: #F28300;
                    line-height: px2rem(50);
                    .ui-symbol {
                        width: px2rem(35);
                        height: px2rem(35);
                    }
                    .txt {
                        font-family: PingFangSC-Regular;
                        font-size: px2rem(36);
                    }
                }
            }
        }
    }

    /*alert提示框*/
    .alert-txt {
        line-height: px2rem(52);
        padding: 0 px2rem(25);
        font-size: px2rem(32);
        font-family: PingFangSC-Regular;
    }

    .icon-wrap {
        margin-left: px2rem(10);
        color: #B2B2B2;
        i {
            line-height: px2rem(100);
        }
    }

    .icon-lianxiren {
        font-size: px2rem(44);
    }

    .icon-right {
        font-size: px2rem(24);
    }

    .icon-arrow-bottom {
        transition: all .3s;
        display: block;
        &.active {
            transform: rotate(180deg);
            -ms-transform: rotate(180deg); /* IE 9 */
            -moz-transform: rotate(180deg); /* Firefox */
            -webkit-transform: rotate(180deg); /* Safari 和 Chrome */
            -o-transform: rotate(180deg); /* Opera */
        }
    }

    .fade-down-enter-active, .fade-down-leave-active {
        transition: all .3s;
    }

    .fade-down-enter, .fade-down-leave-to {
        margin-top: -100%;
        opacity: .5;
    }

    .fade-down-enter-to, .fade-down-leave {
        margin-top: 0;
        opacity: 1;
    }
</style>



